import React from 'react';
import {
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Divider,
  Link
} from '@material-ui/core';
import ColorLensIcon from '@material-ui/icons/ColorLens';
import LanguageIcon from '@material-ui/icons/Language';


const Lists = () => {
  return (
    <div>

      <List component='nav'>
        <Link
          href="https://www.mobixtec.com/"
          color="textPrimary"
          underline="none"
        >
          <ListItem button>
            <ListItemIcon>
              <LanguageIcon />
            </ListItemIcon>
            <ListItemText primary='Mobix' />
          </ListItem>
        </Link>

        <Link
          href="https://www.figma.com/file/bLdWjL7pxSdWde02icnhmX/Desafios-da-Mobix?node-id=0%3A1"
          color="textPrimary"
          underline="none"
        >
          <ListItem button>
            <ListItemIcon>
              <ColorLensIcon />
            </ListItemIcon>
            <ListItemText primary='Figma' />
          </ListItem>
        </Link>

        <Divider />


      </List>
    </div>
  )
}

export default Lists