import {createMuiTheme} from '@material-ui/core/styles';
import teal from '@material-ui/core/colors/teal'
import grey from '@material-ui/core/colors/grey'

const theme = createMuiTheme({
  palette: {
    primary: teal,
    secondary: grey
  }
})

export default theme;

